<template>
	<div :style='{"width":"100%","padding":"30px calc((100% - 1200px)/2)","margin":"0px auto","position":"relative","background":"#EDF0FA"}'>
		<div class="back_box">
			<el-button class="backBtn" size="mini" @click="backClick">
				<span class="icon iconfont icon-jiantou33"></span>
				<span class="text"></span>
			</el-button>
		</div>
		<div class="section-title" :style='{"padding":"0  165px","margin":"0","color":"#44446D","textAlign":"left","flexDirection":"column","background":"url(http://codegen.caihongy.cn/20250118/8fc0638edcbd47b4bdaa28e8495ba36b.png) no-repeat center center / 100% 100%","display":"flex","width":"100%","lineHeight":"100px","fontSize":"32px","justifyContent":"flex-end","height":"100px"}'></div>
		<el-tabs v-model="activeName" @tab-click="handleClick">
			<el-tab-pane label="" name=""></el-tab-pane>
			<el-tab-pane label="" name=""></el-tab-pane>
			<el-tab-pane label="" name=""></el-tab-pane>
			<el-tab-pane label="" name=""></el-tab-pane>
			<el-tab-pane label="" name=""></el-tab-pane>
			<el-tab-pane label="" name=""></el-tab-pane>
		</el-tabs>
		<el-table :data="tableData" style="width: 100%">
			<el-table-column label="" prop="orderid"></el-table-column>
			<el-table-column label="" align="center" width="200px">
				<template slot-scope="scope">
					<div class="shangpin">
						<el-image style="width: 100px; height: 100px;flex: 1" :src="baseUrl + scope.row.picture" fit="fill"></el-image>
						<span style="margin-left: 10px;flex: 1" >{{ scope.row.goodname }}</span>
					</div>
				</template>
			</el-table-column>
			<el-table-column label="">
				<template slot-scope="scope">
					<span v-if="(scope.row.type == 1 || scope.row.type == 3)&&scope.row.price>0"><span :style='{"fontSize":"12px"}'></span>{{ scope.row.price }}</span>
					<span v-if="scope.row.type == 2&&scope.row.price>0">{{ scope.row.price }}</span>
				</template>
			</el-table-column>
			<el-table-column label="" prop="buynumber"></el-table-column>
			<el-table-column label="">
				<template slot-scope="scope">
					<span v-if="(scope.row.type == 1 || scope.row.type == 3)&&scope.row.total>0"><span :style='{"fontSize":"12px"}'></span>{{ scope.row.total }}</span>
					<span v-if="scope.row.type == 2&&scope.row.total>0">{{ scope.row.total }}</span>
				</template>
			</el-table-column>
			<el-table-column label="" prop="address"></el-table-column>
			<el-table-column label="" prop="tel"></el-table-column>
			<el-table-column label="" prop="consignee"></el-table-column>
			<el-table-column label="" prop="addtime"></el-table-column>
			<el-table-column label="" prop="remark"></el-table-column>
			<el-table-column label="" prop="sfsh" v-if="changeReturnGoods()">
				<template slot-scope="scope">
					<el-tag type="success" v-if="scope.row.sfsh==''"></el-tag>
					<el-tag type="warning" v-if="scope.row.sfsh==''"></el-tag>
					<el-tag type="danger" v-if="scope.row.sfsh==''"></el-tag>
				</template>
			</el-table-column>
			<el-table-column label="" prop="shhf"></el-table-column>
			<el-table-column label="" width="150">
				<template slot-scope="scope">
					<el-button v-show="activeName == ''" type="success" :style='{"margin":"2px auto",}' size="mini" @click="pay(scope.row)"></el-button>
					<el-button v-show="activeName == ''" type="danger" :style='{"margin":"2px auto",}' size="mini" @click="cancel(scope.row)"></el-button>
					<el-button v-show="activeName == ''" type="danger" :style='{"margin":"2px auto",}' size="mini" @click="refund(scope.row)">{{scope.row.total>0?'':''}}</el-button>
					<el-button v-if="scope.row.logistics" type="warning" :style='{"margin":"2px auto",}' size="mini" @click="logistics(scope.row)"></el-button>
					<el-button v-if="activeName == ''&&scope.row.sfsh==''&&changeReturnGoods()" type="danger" :style='{"margin":"2px auto",}' size="mini" @click="returnGood(scope.row)"></el-button>
					<el-button v-show="activeName == ''" type="primary" :style='{"margin":"2px auto",}' size="mini" @click="toDetail(scope.row)"></el-button>
					<el-button v-show="activeName == ''" type="success" :style='{"margin":"2px auto",}' size="mini" @click="confirm(scope.row)"></el-button>
				</template>
			</el-table-column>
		</el-table>
		<el-pagination
			background
			id="pagination" class="pagination"
			:pager-count="7"
			:page-size="pageSize"
			:page-sizes="pageSizes"
			prev-text=""
			next-text=""
			:hide-on-single-page="true"
			:layout='["total","prev","pager","next"].join()'
			:total="total"
			:style='{"padding":"0","margin":"10px auto","whiteSpace":"nowrap","color":"#333","textAlign":"center","width":"100%","fontWeight":"500","order":"50"}'
			@current-change="curChange"
			@size-change="sizeChange"
			@prev-click="prevClick"
			@next-click="nextClick"
			></el-pagination>

		<el-dialog title="" :visible.sync="logisticsVisible" width="30%">
			<div class="ql-snow ql-editor" v-html="logisticsText"></div>
		</el-dialog>
		<el-dialog title="" :visible.sync="returngoodVisible" width="30%">
			<el-input v-model="returnGoodForm.returnreason" type="textarea" placeholder=""></el-input>
			<div style="margin: 20px 0 0">
				<el-button type="primary" style="margin-left: 10px;" @click="returnGoodSave"></el-button>
				<el-button type="danger" style="margin-left: 10px;" @click="returngoodVisible=false"></el-button>
			</div>
		</el-dialog>
	
	</div>
</template>

<script>
	import config from '@/config/config'
	export default {
		data() {
			return {
				layouts: '',
				baseUrl: config.baseUrl,
				activeName: '',
				tableData: [],
				total: 1,
				pageSize: 10,
				pageSizes: [],
				totalPage: 1,
				session: {},
				userTableName: localStorage.getItem('UserTableName'),
				logisticsVisible: false,
				logisticsText: '',
				returngoodVisible: false,
				returnGoodForm: {},
			}
		},
		created() {
			this.getSession();
			this.getMyOrderList(1);
		},
		methods: {
			backClick() {
				this.$router.push('/index/center')
			},
			getSession() {
				this.$http.get(this.userTableName+'/session', {emulateJSON: true}).then(res => {
					if (res.data.code == 0) {
						this.session = res.data.data;
					}
				});
			},
			handleClick(tab, event) {
				tab.activeName = event.target.outerText;
				this.getMyOrderList(1);
			},
			toDetail(row) {
				this.$router.push({path: '/index/'+row.tablename+'Detail', query: {id: row.goodid}});
			},
			handleAction(index, row) {
				if (index == '' && this.session.money < row.total) {
					this.$message.error('');
					return;
				}
	
				row.status = index;
				this.$http.post('orders/update', row).then(res => {
					if (res.data.code == 0) {
						if (index == '') this.session.money -= row.total;
						if (index == '' && row.type == 1) this.session.money += row.total;
						if (index == '' && row.type == 2) this.session.jifen += row.total;
						if (index == '' || index == '') {
							this.session.money = this.session.money.toFixed(2);
							this.$http.post(this.userTableName+'/update', this.session).then(res => {});
						}
						this.$message({
							message: '',
							type: 'success',
							duration: 1500,
							onClose: () => {
								this.getMyOrderList(1);
							}
						});
					}
				});
			},
			// 
			refund(item) {
				this.$confirm(`${item.total>0?'':''}`, "", {
					confirmButtonText: "",
					cancelButtonText: "",
					type: "warning"
				}).then(() => {
					// 
					this.$http.get(item.tablename+'/info/'+item.goodid, {}).then(res => {
						// 
						let good = res.data.data;
						// 
						let table = localStorage.getItem("UserTableName");
						this.$http.get(table+'/session', {}).then(res => {
							this.user = res.data.data;
							if(item.type==1||item.type==3) {
								// 
								if (good.jf) {
									this.user.jf = (Number(this.user.jf) - Number(item.total)).toFixed(2);
								}
								this.user.money = (Number(this.user.money) + Number(item.total)).toFixed(2);
								// 
								this.$http.post(table+'/update', this.user).then(res => {
									item.status = ''
									localStorage.setItem('sessionForm',JSON.stringify(this.user))
									this.$http.post(`orders/update`, item).then(res => {
										if(item.tablename == `shangpinxinxi`){
											// 
											good.alllimittimes = good.alllimittimes + item.buynumber;
											this.$http.post(`shangpinxinxi/update`, good).then(res => {
											});
										}
										this.$message({
											message: `${item.total>0?'':''}`,
											type: 'success',
											duration: 1500,
											onClose: () => {
												this.getMyOrderList(1);
											}
										});
									});
								});
							} else if(item.type==2) {
								this.user.jf = Number(this.user.jf) + Number(item.total);
								// 
								this.$http.post(table+'/update', this.user).then(res => {
									item.status = ''
									localStorage.setItem('sessionForm',JSON.stringify(this.user))
									this.$http.post(`orders/update`, item).then(res => {
										if(item.tablename == `shangpinxinxi`){
											// 
											good.alllimittimes = good.alllimittimes + item.buynumber;
											this.$http.post(`shangpinxinxi/update`, good).then(res => {
											});
										}
										this.$message({
											message: `${item.total>0?'':''}`,
											type: 'success',
											duration: 1500,
											onClose: () => {
												this.getMyOrderList(1);
											}
										});
									});
								});
							}
						});
					});
				});
			},
			//
			logistics(row) {
				this.logisticsText = row.logistics;
				this.logisticsVisible = true;
			},
			//
			returnGood(item){
				this.returnGoodForm = item
				this.returngoodVisible = true
			},
			returnGoodSave() {
				this.$confirm(``, "", {
					confirmButtonText: "",
					cancelButtonText: "",
					type: "warning"
				}).then(() => {
					this.returnGoodForm.sfsh = ''
					this.$http.post(`orders/update`, this.returnGoodForm).then(res => {
						this.$message({
							message: '',
							type: 'success',
							duration: 1500,
							onClose: () => {
								this.getMyOrderList(1);
								this.returngoodVisible = false
							}
						});
					})
				});
			},

			// 
			pay(item) {
				// 
				this.$http.get(item.tablename+'/info/'+item.goodid, {}).then(res => {
					// 
					let data = res.data.data;
					// 
					let table = localStorage.getItem("UserTableName");
					this.$http.get(table+'/session', {}).then(res => {
						this.user = res.data.data;
						// 
						if (Number(this.user.money) < Number(item.total)) {
							this.$message({
								message: '',
								type: 'error',
								duration: 1500
							});
							return
						}
						// 
						if (data.jf) {
							this.user.jf = Number((Number(this.user.jf) + Number(item.total)).toFixed(2));
						}
						this.user.money = Number((Number(this.user.money) - Number(item.total)).toFixed(2));
						// 
						this.$http.post(table+'/update', this.user).then(res => {
							item.status = ''
							localStorage.setItem('sessionForm',JSON.stringify(this.user))
							this.$http.post(`orders/update`, item).then(res => {
								this.$message({
									message: '',
									type: 'success',
									duration: 1500,
									onClose: () => {
										this.getMyOrderList(1);
									}
								});
							});
						});
					});
				});
			},
			confirm(item){
				this.$confirm(``, "", {
					confirmButtonText: "",
					cancelButtonText: "",
					type: "warning"
				}).then(() => {
					item.status = ''
					this.$http.post(`orders/update`, item).then(res => {
						this.$message({
							message: '',
							type: 'success',
							duration: 1500,
							onClose: () => {
								this.getMyOrderList(1);
							}
						});
					});
				});
			},
			cancel(item){
				this.$confirm(``, "", {
					confirmButtonText: "",
					cancelButtonText: "",
					type: "warning"
				}).then(() => {
					item.status = ''
					this.$http.post(`orders/update`, item).then(res => {
						// 
						this.$http.get(item.tablename+'/info/'+item.goodid, {}).then(res => {
							// 
							let good = res.data.data;
							if(item.tablename == `shangpinxinxi`){
								// 
								good.alllimittimes = good.alllimittimes + item.buynumber;
								this.$http.post(`shangpinxinxi/update`, good).then(res => {
								});
							}
							this.$message({
								message: '',
								type: 'success',
								duration: 1500,
								onClose: () => {
									this.getMyOrderList(1);
								}
							});
						});
					});
				});
			},
			getMyOrderList(page) {
				this.$http.get('orders/list', {params: {page, limit: this.pageSize,sort:'addtime',order:'desc', userid: Number(localStorage.getItem('frontUserid')), status: this.activeName}}).then(res => {
					if (res.data.code == 0) {
						this.tableData = res.data.data.list;
						this.total = res.data.data.total;
						this.pageSize = Number(res.data.data.pageSize);
						this.totalPage = res.data.data.totalPage;
						if(this.pageSizes.length==0){
							this.pageSizes = [this.pageSize, this.pageSize*2, this.pageSize*3, this.pageSize*5];
						}
					}
				});
			},
			curChange(page) {
				this.getMyOrderList(page);
			},
			prevClick(page) {
				this.getMyOrderList(page);
			},
			sizeChange(size){
				this.pageSize = size
				this.getMyOrderList(1);
			},
			nextClick(page) {
				this.getMyOrderList(page);
			}
		}
	}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
	.section {
		width: 1000px;
		margin: 0 auto;
	}

	.shangpin {
		display: flex;
	}
	
</style>
