<template>
	<div :style='{"width":"100%","padding":"30px calc((100% - 1200px)/2)","margin":"0px auto","position":"relative","background":"#EDF0FA"}'>
		<div class="section-title" :style='{"padding":"0  165px","margin":"0","color":"#44446D","textAlign":"left","flexDirection":"column","background":"url(http://codegen.caihongy.cn/20250118/8fc0638edcbd47b4bdaa28e8495ba36b.png) no-repeat center center / 100% 100%","display":"flex","width":"100%","lineHeight":"100px","fontSize":"32px","justifyContent":"flex-end","height":"100px"}'></div>
		<div class="back_box">
			<el-button class="backBtn" size="mini" @click="backClick">
				<span class="icon iconfont icon-jiantou33"></span>
				<span class="text"></span>
			</el-button>
		</div>
		<el-form class="add-update-preview" :model="form" :rules="rules" ref="form" label-width="180px">
			<el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="title">
				<el-input v-model="form.title" placeholder=""></el-input>
			</el-form-item>
			<el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="typename">
				<el-select v-model="form.typename" placeholder="">
					<el-option
						v-for="(item,index) in categoryList"
						:key="index"
						:label="item.typename"
						:value="item.typename">
					</el-option>
				</el-select>
			</el-form-item>
			<el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="cover">
				<file-upload
					tip=""
					action="file/upload"
					:limit="3"
					:multiple="true"
					:fileUrls="form.cover?form.cover:''"
					@change="coverUploadChange"
					></file-upload>
			</el-form-item>
			<el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="isdone">
				<el-radio-group v-model="form.isdone">
					<el-radio label=""></el-radio>
					<el-radio label=""></el-radio>
				</el-radio-group>
			</el-form-item>
			<el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="isanon">
				<el-radio-group v-model="form.isanon">
					<el-radio :label="1"></el-radio>
					<el-radio :label="0"></el-radio>
				</el-radio-group>
			</el-form-item>
			<el-form-item :style='{"width":"100%","padding":"10px","margin":"0 0 10px","background":"none","display":"inline-block"}' label="" prop="content">
				<editor
					myQuillEditor="content"
					:style='{"minHeight":"400px","border":"1px solid #44446D","outline":"none","color":"#333","borderRadius":"4px","width":"100%","lineHeight":"32px","fontSize":"14px"}'
					v-model="form.content" 
					class="editor" 
					action="file/upload">
				</editor>
			</el-form-item>
			<el-form-item :style='{"padding":"0","margin":"0","textAlign":"center","justifyContent":"center","display":"flex"}'>
				<el-button :style='{"border":"0","cursor":"pointer","padding":"0 35px","margin":"0 20px 0 0","outline":"none","borderRadius":"20px","background":"#44446D","display":"inline-block","width":"150px","lineHeight":"50px","fontSize":"14px","height":"50px"}' type="primary" @click="submitForm('form')">
					<span class="icon iconfont icon-kaitongfuwu" :style='{"color":"rgba(255, 255, 255, 1)","display":"none"}'></span>
					<span class="text" :style='{"color":"rgba(255, 255, 255, 1)"}'>{{this.isEdit ? '' : ''}}</span>
				</el-button>
				<el-button :style='{"border":"0","cursor":"pointer","padding":"0 35px","margin":"0 20px 0 0","outline":"none","borderRadius":"20px","background":"#BBB09B","display":"inline-block","width":"150px","lineHeight":"50px","fontSize":"14px","height":"50px"}' @click="resetForm('form')">
					<span class="icon iconfont icon-shanchu1" :style='{"color":"rgba(64, 158, 255, 1)","display":"none"}'></span>
					<span class="text" :style='{"color":"#fff"}'></span>
				</el-button>
			</el-form-item>
		</el-form>
	</div>
</template>

<script>
	export default {
		//
		data() {
			return {
				form: {
					title: '',
					isdone: '',
					content: '',
					parentid: 0,
					userid: Number(localStorage.getItem('frontUserid')),
					username: localStorage.getItem('username'),
					toptime: '',
					cover: '',
					isanon: 0
				},
				editorOption: {
					modules: {
						toolbar: [
							["bold", "italic", "underline", "strike"],
							["blockquote", "code-block"],
							[{ header: 1 }, { header: 2 }],
							[{ list: "ordered" }, { list: "bullet" }],
							[{ script: "sub" }, { script: "super" }],
							[{ indent: "-1" }, { indent: "+1" }],
							[{ direction: "rtl" }],
							[{ size: ["small", false, "large", "huge"] }],
							[{ header: [1, 2, 3, 4, 5, 6, false] }],
							[{ color: [] }, { background: [] }],
							[{ font: [] }],
							[{ align: [] }],
							["clean"],
							["image", "video"]
						]
					}
				},
				isEdit: false,
				rules: {
					title: [
						{ required: true, message: '', trigger: 'blur' }
					],
					typename: [
						{ required: true, message: '', trigger: 'blur' }
					]
				},
				categoryList: [],
			}
		},
		created() {
			this.getCategoryList()
			if (this.$route.query.id != undefined) {
				this.isEdit = true;
				this.$http.get('forum/detail/' + this.$route.query.id,{}).then(res=>{
					if(res.data.code==0){
						this.form = res.data.data
					}
				})
			}
		},
		//
		methods: {
			getCategoryList(){
				this.$http.get('forumtype/list', {}).then(res => {
					if (res.data.code == 0) {
						this.categoryList = res.data.data.list
					}
				});
			},
			// 
			backClick(){
				history.back()
			},
			onEditorReady(editor) {
				editor.root.setAttribute('data-placeholder', "...");
			},
			coverUploadChange(fileUrls) {
				this.form.cover = fileUrls.replace(new RegExp(this.$config.baseUrl,"g"),"");
			},
			submitForm(formName) {
				this.$refs[formName].validate((valid) => {
					if (valid) {
						if(!this.isEdit){
							this.form.toptime = this.getCurDateTime()
						}
						if(this.form.cover==''){
							this.$message.error('')
							return false
						}
						this.$http.post(`forum/${this.isEdit ? 'update' : 'add'}`, this.form).then(res => {
							if (res.data.code === 0) {
								this.$message({
									message: `${this.isEdit ? '' : ''}`,
									type: 'success',
									duration: 1500,
									onClose: () => {
										if (this.isEdit) {
											this.$router.push('/index/myForumList');
										} else {
											this.$router.push('/index/forum');
										}
									}
								});
							} else {
								this.$message.error(res.data.msg);
							}
						});
					} else {
						return false;
					}
				});
			},
			resetForm(formName) {
				this.form = {
					title: '',
					isdone: '',
					content: '',
					parentid: 0,
					userid: Number(localStorage.getItem('frontUserid')),
					username: localStorage.getItem('username'),
					toptime: '',
					cover: '',
					isanon: 0
				}
			}
		}
	}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
	.add-update-preview .el-form-item /deep/ .el-form-item__label {
		padding: 0 10px 0 0;
		margin: 0;
		color: #2C3657;
		font-weight: 500;
		width: 180px;
		font-size: 16px;
		line-height: 50px;
		text-align: right;
	}
	.add-update-preview .el-form-item /deep/ .el-form-item__content {
		margin-left: 180px;
	}
	.add-update-preview .el-input /deep/ .el-input__inner {
		border: 0px solid #BBB09B;
		border-radius: 10px;
		padding: 0 12px;
		box-shadow: 0 0 0px rgba(64, 158, 255, .5);
		outline: none;
		color: #000;
		background: #FFFFFF;
		width: 100%;
		font-size: 15px;
		line-height: 50px;
		height: 50px;
	}
	.add-update-preview .el-select /deep/ .el-input__inner {
		border: 0px solid #BBB09B;
		border-radius: 10px;
		padding: 0 10px;
		box-shadow: 0 0 0px rgba(64, 158, 255, .5);
		outline: none;
		color: #000;
		width: 100%;
		font-size: 15px;
		height: 50px;
	}
  
	.add-update-preview .el-date-editor /deep/ .el-input__inner {
		border: 0px solid #BBB09B;
		border-radius: 10px;
		padding: 0 10px 0 30px;
		box-shadow: 0 0 0px rgba(64, 158, 255, .5);
		outline: none;
		color: #000;
		width: 100%;
		font-size: 15px;
		height: 50px;
	}
  
	.add-update-preview /deep/ .el-upload--picture-card {
		background: transparent;
		border: 0;
		border-radius: 0;
		width: auto;
		height: auto;
		line-height: initial;
		vertical-align: middle;
	}
  
	.add-update-preview /deep/ .upload .upload-img {
		border: 0px solid #BBB09B;
		cursor: pointer;
		border-radius: 6px;
		color: #000;
		background: #FFFFFF;
		width: 150px;
		font-size: 32px;
		line-height: 60px;
		text-align: center;
		height: 60px;
	}
  
	.add-update-preview /deep/ .el-upload-list .el-upload-list__item {
		border: 0px solid #BBB09B;
		cursor: pointer;
		border-radius: 6px;
		color: #000;
		background: #FFFFFF;
		width: 150px;
		font-size: 32px;
		line-height: 60px;
		text-align: center;
		height: 60px;
	}
  
	.add-update-preview /deep/ .el-upload .el-icon-plus {
		border: 0px solid #BBB09B;
		cursor: pointer;
		border-radius: 6px;
		color: #000;
		background: #FFFFFF;
		width: 150px;
		font-size: 32px;
		line-height: 60px;
		text-align: center;
		height: 60px;
	}
	.add-update-preview /deep/ .el-upload__tip {
		color: #333;
		line-height: 1;
	}
  
	.add-update-preview .el-textarea /deep/ .el-textarea__inner {
		border: 0px solid #BBB09B;
		border-radius: 10px;
		padding: 12px;
		box-shadow: 0 0 0px rgba(64, 158, 255, .5);
		outline: none;
		color: #000;
		width: 100%;
		font-size: 14px;
		height: 120px;
	}
</style>
