<template>
	<div class="forum-preview">
		<div class="forum-title">
			<div></div>
		</div>
		<el-form :inline="true" :model="formSearch" class="list-form-pv">
			<el-form-item class="search-item">
				<el-input v-model="title" placeholder=""></el-input>
			</el-form-item>
			<div class="search-btn-item">
				<el-button class="searchBtn" type="primary" @click="getForumList(1)">
					<span class="icon iconfont icon-shouye-zhihui"></span>
					
				</el-button>
				<el-button class="pubBtn" type="primary" @click="toForumAdd">
					<span class="icon iconfont icon-shouye-zhihui"></span>
					
				</el-button>
			</div>
		</el-form>
		<div class="category-list">
			<div class="item" @click="categoryClick(0)" :class="categoryIndex == 0 ? 'active' : ''"></div>
			<div v-for="(item,index) in categoryList" @click="categoryClick(index+1)" :key="index" class="item" :class="categoryIndex == index+1 ? 'active' : ''">{{item.typename}}</div>
		</div>
		<div class="z-box">
			<div class="section-content" v-for="item in forumList" :key="item.id" @click.stop="toForumDetail(item)">
				<img class="item-img" :src="$config.baseUrl + item.cover.split(',')[0]" @click.stop="imgPreView($config.baseUrl + item.cover.split(',')[0])" />
				<div class="item-title">{{item.title}}</div>
				<div class="item-user">{{item.isanon==1&&item.userid!=userid?'':item.username}}</div>
				<div class="item-time">{{item.addtime}}</div>
			</div>
		</div>
		<el-pagination
			background
			id="pagination" class="pagination"
			:pager-count="7"
			:page-size="pageSize"
			:page-sizes="pageSizes"
			prev-text=""
			next-text=""
			:hide-on-single-page="true"
			:layout='["total","prev","pager","next"].join()'
			:total="total"
			@current-change="curChange"
			@size-change="sizeChange"
			@prev-click="prevClick"
			@next-click="nextClick"
			></el-pagination>
		<el-dialog title="" :visible.sync="previewVisible" width="50%">
			<img :src="previewImg" alt="" style="width: 100%;">
		</el-dialog>
	</div>
</template>

<script>
	export default {
		//
		data() {
			return {
				formSearch: {},
				title: '',
				layouts: '',
				forumList: [],
				total: 1,
				pageSize: 10,
				pageSizes: [],
				totalPage: 1,
				previewImg: '',
				previewVisible: false,
				userid: Number(localStorage.getItem('frontUserid')),
				categoryIndex: 0,
				categoryList: [],
			}
		},
		created() {
			this.getCategoryList()
		},
		watch:{
			$route(newValue){
				this.getCategoryList()
			}
		},
		//
		methods: {
			getCategoryList(){
				this.$http.get('forumtype/list', {}).then(res => {
					if (res.data.code == 0) {
						this.categoryList = res.data.data.list
						if(this.$route.query.homeFenlei){
							for(let i=0;i<this.categoryList.length;i++) {
								if(this.$route.query.homeFenlei == this.categoryList[i].typename) {
									this.categoryIndex = i + 1;
									const currentRoute = this.$route;
									const routeWithoutQuery = { ...currentRoute };
									delete routeWithoutQuery.query;
									this.$router.replace(routeWithoutQuery)
									break;
								}
							}
						}
						this.getForumList(1);
					}
				});
			},
			categoryClick(index) {
				this.categoryIndex = index
				this.getForumList()
			},
			imgPreView(url){
				this.previewImg = url
				this.previewVisible = true
			},
			getForumList(page) {
				let params = {page, limit: this.pageSize, isdone: '', sort: 'istop,toptime', order: 'desc,desc',delflag: 0};
				let searchWhere = {};
				if(this.title != '') searchWhere.title = '%' + this.title + '%';
				if(this.categoryIndex!=0){
					searchWhere.typename = this.categoryList[this.categoryIndex - 1].typename
				}
				this.$http.get('forum/flist', {params: Object.assign(params, searchWhere)}).then(res => {
					if (res.data.code == 0) {
						this.forumList = res.data.data.list;
						this.total = res.data.data.total;
						this.pageSize = Number(res.data.data.pageSize);
						this.totalPage = res.data.data.totalPage;
						if(this.pageSizes.length==0){
							this.pageSizes = [this.pageSize, this.pageSize*2, this.pageSize*3, this.pageSize*5];
						}
					}
				});
			},
			curChange(page) {
				this.getForumList(page);
			},
			prevClick(page) {
				this.getForumList(page);
			},
			sizeChange(size){
				this.pageSize = size
				this.getForumList(1);
			},
			nextClick(page) {
				this.getForumList(page);
			},
			toForumAdd() {
				this.$router.push('/index/forumAdd');
			},
			toForumDetail(item) {
				this.$router.push({path: '/index/forumDetail', query: {id: item.id}});
			}
		}
	}
</script>

<style rel="stylesheet/scss" lang="scss" scoped>
	.forum-preview {
				padding: 30px calc((100% - 1200px)/2);
				margin: 0px auto;
				background: #EDF0FA;
				width: 100%;
				position: relative;
				.forum-title {
						width: 100%;
						div {
								padding: 0  165px;
								margin: 0px auto 20px ;
								flex-direction: column;
								color: #44446D;
								background: url(http://codegen.caihongy.cn/20250118/8fc0638edcbd47b4bdaa28e8495ba36b.png) no-repeat center center / 100% 100%;
								display: flex;
								width: 1200px;
								font-size: 32px;
								line-height: 100px;
								justify-content: flex-end;
								text-align: left;
								height: 100px;
							}
		}
		.list-form-pv {
						padding: 10px;
						margin: 0  auto;
						background: none;
						display: flex;
						width: 1200px;
						align-items: center;
						flex-wrap: wrap;
						height: auto;
						order: 1;
						.search-item {
								margin: 0 10px;
								/deep/ .el-form-item__content {
										display: flex;
										align-items: center;
									}
				.el-input {
										width: 100%;
									}
				.el-input /deep/ .el-input__inner {
										border: 1px solid #44446D;
										border-radius: 20px;
										padding: 0 10px;
										margin: 0;
										color: #333;
										width: 100%;
										font-size: 14px;
										line-height: 42px;
										height: 42px;
									}
			}
			.search-btn-item {
								display: flex;
								.searchBtn {
										cursor: pointer;
										border: 0;
										padding: 0px 15px;
										margin: 0 10px 0 0;
										color: #fff;
										font-size: 14px;
										line-height: 42px;
										border-radius: 30px;
										outline: none;
										background: #FFD99D;
										width: 120px;
										text-align: center;
										height: 42px;
										.icon {
												margin: 0 10px 0 0;
												color: #fff;
												display: none;
											}
				}
				
				.searchBtn:hover {
										opacity: 0.5;
									}
				
				.pubBtn {
										cursor: pointer;
										border: 0;
										border-radius: 30px;
										padding: 0px 25px;
										margin: 0;
										outline: none;
										color: #fff;
										background: #44446D;
										width: auto;
										font-size: 14px;
										line-height: 42px;
										height: 42px;
										.icon {
												margin: 0 10px 0 0;
												color: #fff;
												display: none;
												font-size: 14px;
											}
				}
				
				.pubBtn:hover {
										opacity: 0.5;
									}
			}
		}
		.category-list {
						border-radius: 0 0 20px 20px;
						padding: 20px 10px;
						margin: 0 0 20px 0;
						background: #FFD99D;
						display: flex;
						width: 100%;
						flex-wrap: wrap;
						height: auto;
						order: 3;
						.item {
								cursor: pointer;
								border-radius: 0;
								padding: 0 15px;
								max-height: 40px;
								margin: 0 30px 0px 20px;
								color: #44446D;
								background: #EDF0FA;
								display: flex;
								min-height: 40px;
								justify-content: center;
								align-items: center;
								min-width: 150px;
							}
			
			.item:hover {
								color: #fff;
								background: #44446D;
								text-align: center;
							}
			
			.item.active {
								color: #fff;
								background: #44446D;
								text-align: center;
							}
		}
		.z-box {
						border: 0px solid #BBB09B;
						border-radius: 0px;
						background: #CCDAF1;
						width: 100%;
						.section-content {
								cursor: pointer;
								padding: 5px 20px;
								margin: 0;
								display: flex;
								border-color: #44446D;
								border-radius: 0;
								background: #CCDAF1;
								width: 100%;
								justify-content: space-between;
								border-width: 0 0 1px 0;
								align-items: center;
								position: relative;
								border-style: solid;
								.item-img {
										border-radius: 50%;
										margin: 0 5px 10px 0;
										bottom: 20px;
										width: 30px;
										transition: all .4s;
										height: 30px;
										order: 4;
									}
				.item-title {
										color: #000000;
										flex: 1;
										font-size: 15px;
										order: 3;
									}
				.item-user {
										color: #000000;
										font-size: 14px;
										line-height: 1.5;
										order: 5;
									}
				.item-time {
										margin: 0 0 0 80px;
										color: #000000;
										width: 180px;
										font-size: 14px;
										order: 6;
									}
			}
			.section-content:hover {
								opacity: 0.8;
								.item-title {
										color: #000;
									}
				.item-user {
										color: #000;
										font-weight: bold;
									}
				.item-time {
										color: #111;
									}
			}
		}
	}
</style>
